@charset "UTF-8";
/**
 * For modern browsers
 * 1. The space content is one way to avoid an Opera bug when the
 *    contenteditable attribute is included anywhere else in the document.
 *    Otherwise it causes space to appear at the top and bottom of elements
 *    that are clearfixed.
 * 2. The use of `table` rather than `block` is only necessary if using
 *    `:before` to contain the top-margins of child elements.
 */
.clearfix::before,
.clearfix::after {
  content: " "; /* 1 */
  display: table; /* 2 */
}

.clearfix::after {
  clear: both;
}

/**
 * For IE 6/7 only
 * Include this rule to trigger hasLayout and contain floats.
 */
.clearfix {
  *zoom: 1;
}

.row {
  padding: 18px 0;
  background-color: #ffffff;
}

.row--tall {
  padding: 24px;
}

.row--flush {
  padding: 0;
}

.row--tablet-thirds,
.row--thirds,
.row--halves {
  padding-top: 0;
  padding-bottom: 0;
}

.row--tablet-thirds .row__heading,
.row--thirds .row__heading {
  padding-top: 18px;
}

.row--tablet-thirds .row__column,
.row--thirds .row__column,
.row--halves .row__column {
  padding-top: 18px;
  padding-bottom: 18px;
}

.row--alt {
  background-color: #f5f5f5;
  color: #333333;
}

.row--brand {
  background-color: #0078c9;
  color: #ffffff;
}

.row--secondary {
  background-color: #ffeb05;
  color: #333333;
}

.row__heading {
  width: 100%;
  margin: 0 0 18px 0;
  font-family: "Oswald", sans-serif;
  font-size: 22px;
  line-height: 24px;
  font-weight: 700;
}

.row__heading--flush {
  margin-bottom: 0;
}

.row__sub-heading {
  width: 100%;
  margin: 8px 0;
  font-family: "Oswald", sans-serif;
  font-size: 16px;
  line-height: 18px;
  font-weight: 700;
}

.row__column-image {
  width: 75%;
  max-width: 165px;
}

.row__column--flush.row__column--flush {
  padding: 0;
}

/* Only for Desktops */
@media only screen and (min-width: 768px) {
  .row {
    padding: 30px 0;
  }
  .row--flush {
    padding: 0;
  }
  .row--tall {
    padding: 36px 0;
  }
  .row__heading {
    margin-bottom: 32px;
    font-size: 26px;
    line-height: 30px;
  }
  .row__sub-heading {
    margin: 10px 0;
    font-size: 18px;
    line-height: 20px;
  }
  .row--tablet-thirds > .container,
  .row--halves > .container {
    display: flex;
    align-items: center;
  }
  .row--tablet-thirds .row__heading,
  .row--halves .row__heading {
    padding-top: 0;
  }
  .row--tablet-thirds .row__column {
    width: 33%;
    padding: 0;
  }
  .row--tablet-thirds .row__column:first-child {
    padding-right: 15px;
  }
  .row--tablet-thirds .row__column:not(:first-child):not(:last-child) {
    padding-right: 7.5px;
    padding-left: 7.5px;
  }
  .row--tablet-thirds .row__column:last-child {
    padding-left: 15px;
  }
  .row--halves .row__column {
    width: 50%;
    padding: 0;
  }
  .row--halves .row__column:first-child {
    padding-right: 15px;
  }
  .row--halves .row__column:last-child {
    padding-left: 15px;
  }
}
@media only screen and (min-width: 1200px) {
  .row--thirds > .container {
    display: flex;
    align-items: center;
  }
  .row--thirds .row__column {
    width: 33%;
    padding-top: 0;
    padding-bottom: 0;
  }
  .row--thirds .row__column:first-child {
    padding-right: 30px;
  }
  .row--thirds .row__column:not(:first-child):not(:last-child) {
    padding: 0 15px;
  }
  .row--thirds .row__column:last-child {
    padding-left: 30px;
  }
  .row--tall {
    padding: 44px 0;
  }
  .row__heading {
    margin-bottom: 40px;
  }
}
.main-content {
  display: flex;
  transition: all 1s ease-in-out;
}

.nav + .main-content {
  padding-top: 46px;
}

.main-content__inner {
  width: 100%;
}

/* For all devices */
.footer {
  background-color: #f5f5f5;
  color: #7b8676;
  border-top: 1px solid #eeeeee;
  padding: 18px 0;
}

.footer__logo {
  height: 40px;
  border: 1px solid #0078c9;
  border-radius: 50%;
  background-color: #fff;
}

.footer__links,
.footer__legal,
.footer__version {
  display: inline-block;
  width: 100%;
  margin-bottom: 10px;
}

.footer__link {
  position: relative;
  color: #333333;
}
.footer__link::before {
  content: "Â ";
  position: absolute;
  bottom: 0;
  left: 0;
  width: calc(100% - 12px);
  height: 1px;
  border-bottom: transparent solid 1px;
  transition: all 0.3s ease-in-out;
}
.footer__link:last-child::before {
  width: 100%;
}
.footer__link:not(:last-child)::after {
  content: "|";
  color: #7b8676;
  margin: 0 4px;
}
.footer__link:hover, .footer__link:focus {
  color: #1a1a1a;
}
.footer__link:hover::before, .footer__link:focus::before {
  border-bottom-color: #1a1a1a;
}

/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  .footer {
    padding: 34px 0;
  }
  .footer > .container {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
  }
  .footer__logo {
    order: 4;
  }
  .footer__links {
    width: 33%;
    text-align: left;
    order: 1;
  }
  .footer__legal {
    width: 33%;
    text-align: right;
    order: 3;
  }
  .footer__version {
    width: 33%;
    text-align: center;
    order: 2;
    margin: 0 0 16px;
  }
}
/* For all devices */
.button {
  width: auto;
  margin: 5px;
  padding: 6px 12px;
  background-color: #333333;
  color: #ffffff;
  font: inherit;
  border-radius: 2px;
  transition: background-color 0.2s ease-in-out, color 0.2s ease-in-out;
}
.button:hover, .button:focus {
  background-color: #262626;
}

.button--large {
  padding: 8px 16px;
  font-weight: 700;
}

.button--white {
  background-color: #ffffff;
  color: #333333;
}

.button--white:hover,
.button--white:focus {
  background-color: #fcfcfc;
}

.button--clear {
  background-color: transparent;
  color: #ffffff;
}

.button--brand {
  background-color: #0078c9;
  color: #ffffff;
}
.button--brand:hover, .button--brand:focus {
  background-color: #0081D6;
}

.button--primary {
  background-color: transparent;
  color: #0078c9;
  border: 2px solid #0078c9;
}
.button--primary:hover, .button--primary:focus {
  background-color: #0078c9;
  color: #ffffff;
}

.button--secondary {
  background-color: #ffeb05;
  color: #333333;
}
.button--secondary:hover, .button--secondary:focus {
  background-color: #ffef38;
}

.button--clear:hover,
.button--clear:focus,
.button--clear:active,
.button--clear:active:hover,
.button--clear:active:focus {
  background-color: transparent;
  color: #cccccc;
}

a.button {
  display: inline-block;
}

.button:disabled {
  cursor: not-allowed;
  filter: alpha(opacity=65);
  opacity: 0.65;
}

/* Only for Desktops */
@media only screen and (min-width: 1200px) {
  .button {
    margin: 7px;
    padding: 9px 14px;
  }
  .button--large {
    font-size: 18px;
    padding: 12px 24px;
  }
}
.link {
  color: #333333;
  border-bottom: transparent solid 1px;
}

.link:hover,
.link:focus {
  color: #1a1a1a;
  border-bottom-color: #1a1a1a;
}

.link--brand {
  color: #0078c9;
}

.link--brand:hover,
.link--brand:focus {
  color: #0081D6;
  border-bottom-color: #0081D6;
}

.link--secondary {
